import styles from "../component-css/FilterDropdown.module.css";
import { useState, useEffect } from "react";

export const FilterDropdown = ({ handleFilter, handleCheck, filter,loadedCards }) => {
  const [open, toggleOpen] = useState(null);
  const [ecos,setEcos] = useState([])
  
  const handleSubmit = (e) => {
    e.preventDefault();
    handleFilter(filter);
    toggleOpen(!open);
  };

  useEffect(()=>{
    let temp = new Set()
    loadedCards.forEach((card)=>{temp.add(card.props.eco)})
    setEcos([...temp])
  },[])

  return (
    <div>
      <button
        onClick={() => {
          toggleOpen(!open);
        }}
      >
        Filter
      </button>
      {
        <div hidden={!open} className={styles.filterContainer}>
          <div id="categories">
            <label>Categories</label>
            {ecos && ecos.map((eco)=>(<div>
              <input id={eco} type="checkbox" onChange={handleCheck}></input>
              <label htmlFor={eco}>{eco}</label>
            </div>))
            }
            
          </div>
          <div id="maxFee">
            <div>
              <label htmlFor="maxFeeNum">Max Fee</label>
              <input id="maxFeeNum" type="number" onChange={handleCheck} />
            </div>
          </div>
          <button onClick={handleSubmit}>Apply</button>
        </div>
      }
    </div>
  );
};
